.footer {
  width: 100%;
  border-top: 2px dashed silver;
  background: #FFEBCD;

  p {
    text-align: center;
    color: #212529;
    font-size: 1.6rem;
    padding: 20px;
  }
}


@bg-color:yellow;
@font-color:pink;

.custom-btn(@bg-color:#008080, @width:200px, @font-color:white){
  display: block;
  background-color: @bg-color;
  margin: 0 10px 20px;
  cursor: pointer;
  width: @width;
  height: 45px;
  font-size: 2rem;
  border-radius: 10px;
  color:@font-color;
}
  &:hover{
    background-color:@font-color;
    color:@bg-color;
  }
  .btn{
    .custom-btn(green, 200px, white);
  }
